.nav {
  width: 100%;
  z-index: 999;
  height: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  border-bottom: 1px solid transparent;
  will-change: transform;
  transform: translateY(0);
  transition: all 0.5s; }
  .nav a {
    font-weight: 400;
    text-transform: uppercase; }
  .nav__links {
    margin-right: 1rem;
    letter-spacing: 2px; }
    .nav__links > .btn {
      color: rgba(0, 0, 0, 0.64) !important;
      padding: 1rem 0; }
  .nav--right {
    flex: 0 1 50%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 2rem; }
  .nav__share__links {
    display: flex;
    list-style: none; }
    .nav__share__links__item:not(:last-child) {
      margin-right: 1rem; }
  .nav .scroll-color {
    background-color: #fff; }
  .nav.hidden {
    transform: translateY(-100%); }
  .nav__fix {
    background-color: #fff;
    border-bottom: var(--line);
    box-shadow: var(--shadow-elevate-8); }
  .nav__logo {
    height: 3.5rem;
    margin-left: 2rem; }
    @media only screen and (max-width: 37.5em) {
      .nav__logo {
        margin-left: 0.5rem; } }
