.blog__posts {
  padding: 1rem; }
  .blog__posts--container {
    display: flex;
    flex-wrap: wrap;
    align-content: stretch; }
  .blog__posts__item {
    flex: 1 0 calc(100% / 3 - 2rem);
    padding: 0.5rem;
    margin: 0.5rem;
    min-height: 280px;
    display: flex; }
    @media only screen and (max-width: 37.5em) {
      .blog__posts__item {
        flex: 1 0 100%;
        margin: 0; } }
    .blog__posts__item--wrapper {
      display: flex;
      flex-direction: column;
      background-color: #fff;
      box-shadow: var(--shadow-elevate-16); }
  .blog__posts__image-box {
    height: 160px;
    width: 100%;
    margin-bottom: 0rem;
    cursor: pointer;
    overflow: hidden; }
    .blog__posts__image-box:hover .blog__posts__image {
      filter: opacity(0.85);
      transform: scale(1.2); }
  .blog__posts__image {
    background-position: 50% 50%;
    background-size: cover;
    height: 100%;
    width: 100%;
    background-origin: border-box !important;
    transition: all 0.5s; }
  .blog__posts__meta {
    padding-bottom: 0.5rem; }
    .blog__posts__meta .date {
      color: #3b3b3b;
      font-weight: 400;
      font-size: 0.8rem; }
  .blog__posts__content {
    cursor: pointer;
    margin-bottom: 1rem; }
  .blog__posts__title {
    display: inline-block;
    padding-bottom: 0.5rem;
    color: #141414; }
  .blog__posts__desc {
    color: #3b3b3b;
    font-weight: 400;
    font-size: 0.9rem; }
