.circle-wrapper {
  background: repeating-radial-gradient(circle, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05) 1px, #ffff 1px, #fff 50px); }

.read__more {
  font-size: 1rem;
  font-weight: 500;
  color: var(--color-grey-dark-1);
  box-shadow: rgba(255, 196, 0, 0.75) 0px -2px 0px 0px inset;
  font-weight: bold;
  margin-right: 0.2rem;
  border-bottom: 1px solid rgba(255, 196, 0, 0.75);
  transition: all 0.3s;
  cursor: pointer; }
  .read__more span {
    margin-left: 5px;
    transition: margin 0.3s; }
  .read__more:hover {
    background: rgba(255, 196, 0, 0.75); }

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  display: inline-block;
  transition: all 0.2s;
  position: relative;
  font-size: 1rem;
  border: 1px solid transparent;
  cursor: pointer;
  text-align: center;
  color: #141414;
  outline: none;
  transition: all .3s; }

.btn--next {
  background: #141414;
  border-radius: 10rem;
  color: #fff;
  margin-top: 1rem;
  padding: 0.5rem 1.5rem; }
  .btn--next span {
    margin-left: 5px; }

.btn.btn--active-link {
  color: #141414 !important;
  border-bottom: 1px solid #141414 !important;
  box-shadow: #141414 0px -2px 0px 0px inset;
  border-radius: 0; }

.btn.btn-link:hover {
  color: #141414 !important;
  border-bottom: 1px solid #141414 !important;
  box-shadow: #141414 0px -2px 0px 0px inset;
  border-radius: 0; }

.share__button {
  border: 0;
  padding: 0;
  background: transparent;
  cursor: pointer;
  outline: none;
  transition: all .2s; }
  .share__button:hover {
    fill: rgba(255, 196, 0, 0.75); }

.padding--2 {
  padding: 2rem; }

.vertical__title {
  color: #141414;
  display: block;
  text-align: right;
  background-image: linear-gradient(#141414 0%, #141414 100%);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: center center;
  letter-spacing: 1px;
  font-variant: small-caps;
  text-transform: lowercase;
  /* background-position: 13% 16%; */
  text-transform: uppercase;
  line-height: 1;
  /* margin: 0px; */
  font-family: inherit;
  font-size: 1.5rem;
  max-width: 11.5em;
  transform-origin: center right;
  transform: rotateZ(90deg) translateX(6em) translateY(1em);
  letter-spacing: 4px;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%; }
  .vertical__title span {
    display: inline-block;
    /* padding: 0 0.6em; */
    /* background-color: #f6f3f2; */
    position: relative;
    left: 17rem; }
  @media only screen and (max-width: 37.5em) {
    .vertical__title {
      font-size: 1rem;
      max-width: 15.5em;
      letter-spacing: 6px; } }
