.article {
  max-width: 740px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  margin: 0 auto; }
  .article--container {
    padding: 2rem 0rem;
    border-bottom: var(--line);
    min-height: calc(100vh - 4rem) !important; }
    .article--container::after {
      content: "";
      display: table; }
    .article--container::before {
      content: "";
      display: table; }
  .article img {
    background-size: cover;
    max-width: 740px; }
  .article__title {
    font-size: 3rem;
    letter-spacing: -1px;
    line-height: 1.4; }
  .article__summary {
    font-size: 1.5rem;
    letter-spacing: -1px;
    margin-bottom: 2rem;
    font-style: italic;
    color: var(--color-grey-dark-3);
    font-weight: 300; }
  .article__content > div {
    font-size: 1.3rem;
    display: flex;
    align-items: center;
    flex-direction: column; }
  .article__related__posts {
    background: #f9f9f9;
    padding: 2rem; }
    @media only screen and (max-width: 37.5em) {
      .article__related__posts {
        padding: 0; } }
    .article__related__posts .blog__posts {
      max-width: 75rem;
      margin: 0 auto; }
  .article__paragraph {
    margin-bottom: 2rem; }
  .article__line-height--2 {
    line-height: 2 !important; }
  .article__share {
    max-width: 1000px;
    margin: 0 auto; }
    .article__share::after {
      content: "";
      display: table; }
    .article__share::before {
      content: "";
      display: table; }
  .article__share__wrapper {
    top: 0%; }
    .article__share__wrapper--fixed {
      position: fixed !important; }
    .article__share__wrapper--absolute {
      position: absolute !important; }
  .article__share__list {
    list-style: none; }
    .article__share__list__item {
      margin-top: 10px;
      margin-bottom: 10px; }
